import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { categories } from '../categories-mockup';
import { Router } from '@angular/router';

@Component({
  selector: 'app-service-details',
  templateUrl: './service-details.component.html',
  providers: []
})
export class ServiceDetailsComponent implements OnInit {

  private mainCategory: string = "";
  private description: string = "";
  private title: string = "";

  constructor(private router: ActivatedRoute, private routers: Router) {
    router.params.subscribe(params => {
      this.mainCategory = params['category'];
      if(this.mainCategory=="CONSULT OUR TEAM") {
        this.routers.navigate(['contactUs']);
      } else {
        this.getCatDetails(this.mainCategory);
      }
    });
  }

  getCatDetails(mainCategory) {
    for (var i = 0; i < categories.categories.length; i++) {
      if (categories.categories[i].name == mainCategory) {
        this.description = categories.categories[i].description;
        this.title = mainCategory;
        break;
      }
    }
  }

  ngOnInit() {
  }

}



