import { Injectable } from "@angular/core"
import { Http, Headers } from '@angular/http';
import { Observable } from 'rxjs/Observable';

import 'rxjs/add/operator/map';

@Injectable()
export class Users {
    constructor(private http: Http) { }
    getAllUsers(url: string) {
        return this.http.get(url).map(res => res.json());
    }

    getUserByEmail(url: string, email: string) {
        return this.http.get(url + "/" + email).map(res => res.json());
    }

    addUser(url: string, user: any) {
        return this.http.post(url, user).map(res => res.text());
    }

    updateUser(url: string, user: any, id) {

        var params = {
            id: id,
            firstName: user['firstName'],
            lastName: user['lastName'],
            email: user['email'],
            mobile: user['mobile'],
            password: user['password'],
            role: user['role']
        }
        return this.http.post(url, params).map(res => res.text());
    }

    getCurrentUser(url: string) {
        return this.http.get(url).map(res => res.json());
    }
}


