export class FireSystemModel {
    constructor() {
        this.drawings = "";
        this.buildingType = "";
        this.buildingTypeSub = "";
        this.country = "";
        this.landArea = "";
        this.groundArea = "";
        this.noOfFloors = "";
        this.city = "";
        this.additionalRequests = "";
        this.approval = "";
        this.language = "";
        this.comments = "";
    }

    drawings: string;
    buildingType: string;
    buildingTypeSub: string;
    country: string;
    landArea: string;
    groundArea: string;
    noOfFloors: string;
    city: string;
    additionalRequests: string;
    approval: string;
    language: string;
    comments: string;

}


