import { Component, OnInit } from '@angular/core';
import { ContestCost } from '../contest-cost/models/model.contest-cost';
import { RequiredDrawings, BuildingTypes } from '../../drop-down-lists/workshop-drawings';
import { FormSubmit } from '../services/service.submit-form';
import { serviceUrls } from '../../service-urls';
import { Users } from '../../services/service.get-users';
import { ActivatedRoute } from '@angular/router';
import { FileUpload } from '../services/service.file-upload';

@Component({
  selector: 'app-tenders-revision',
  templateUrl: './tenders-revision.component.html',
  providers: [FormSubmit, FileUpload]
})
export class TendersRevisionComponent implements OnInit {

  private contestCostModel = new ContestCost();

  private formData;

  constructor(private _FormSubmit: FormSubmit, private _Users: Users, private _ActivatedRoute: ActivatedRoute, private _FileUpload: FileUpload) { }

  private otherText = false;
  private validForm = true;
  private viewOnly: boolean = false;

  private currentUser;
  private orderStatus: string = "pending";
  private attachments: string[] = [];
  private uploadedFiles;

  private drawingsList = RequiredDrawings;
  private buildingTypes = BuildingTypes;
  private buildingTypesSub = [];

  ngOnInit() {
    if (localStorage.getItem('formData')) {
      this.orderStatus = localStorage.getItem('orderStatus');
      this.attachments = localStorage.getItem('attachments').split(",");
      this.contestCostModel = JSON.parse(localStorage.getItem('formData'));
      if (localStorage.getItem('orderStatus') != 'processed') {
        this.viewOnly = false;
      } else {
        this.viewOnly = true;
      }
      if (this.contestCostModel.buildingType == "Other") {
        this.otherText = true;
      } else {
        this.buildingTypesSub.push(this.contestCostModel.buildingTypeSub);
      }
    }
    this.getCurrentUserInfo();
  }

  getFiles(e) {
    this.uploadedFiles = e;
    this.attachments = [];
    for (var file of e.target.files) {
      this.attachments.push(file.name);
    }
    console.log(this.attachments.toString());
  }

  submitForm() {
    this.validateData();
  }

  validateData() {
    if (this.contestCostModel.buildingType
      && this.contestCostModel.buildingTypeSub
      && this.contestCostModel.city
      && this.contestCostModel.country
      && this.contestCostModel.services
      && this.contestCostModel.noOfFloors
      && this.contestCostModel.additionalRequests
      && this.contestCostModel.buildingSize
      && this.contestCostModel.projectSize && this.attachments) {
      this.validForm = true;
      this.saveForm(JSON.stringify(this.contestCostModel));
    } else {
      this.validForm = false;
    }
  }

  saveForm(formData) {
    this._FileUpload.uploadFiles(this.uploadedFiles).subscribe(
      data => {
        // SHOW A MESSAGE RECEIVED FROM THE WEB API.
        if (data) {
          this._FormSubmit.saveFormDetails(serviceUrls.addOrder, this.currentUser['id'], this.orderStatus, "Review-and-check", this.attachments.toString(), formData).subscribe(
            data => {
              window.location.href = "/myOrders";
            }, error => {
              console.error(error);
            }
          )
        }
      },
      error => {
        console.log(error.MESSAGE);    // SHOW ERRORS IF ANY.
      }
    );
  }

  ngOnDestroy() {
    localStorage.setItem('formData', "");
    localStorage.setItem('orderStatus', "");
    localStorage.setItem('attachments', "");
  }

  getCurrentUserInfo() {
    this._Users.getCurrentUser(serviceUrls.getCurrentUser).subscribe(
      data => {
        if (data["id"]) {
          this.currentUser = data;
        }
      }
    );
  }

}




