import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { categories } from '../categories-mockup';

@Component({
  selector: 'app-service-tabs',
  templateUrl: './service-tabs.component.html',
})
export class ServiceTabsComponent implements OnInit {

  @Output() tabChanged = new EventEmitter<object>();

  private defaultImg = "buildingBg";
  private defaultDesc = "Create & develop a unique design from conceptial to final design drawings.";
  private defaultDescHeader: string = "DESIGN Your DREAM";

  private servicesCategories = categories.categories;

  constructor() { }

  ngOnInit() {
  }

  changeCategoryInfo(categoryDescHeader, categoryDesc, categoryImg) {
    var categoryInfo = {
      descHeader: categoryDescHeader,
      desc: categoryDesc,
      img: categoryImg
    }
    this.tabChanged.emit(categoryInfo);
  }

}



