import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-home-content',
  templateUrl: './home-content.component.html',
})
export class HomeContentComponent implements OnInit {

  private myInnerHeight: any;
  private categoryDescHeader: string = "DESIGN Your DREAM";
  private categoryDesc: string = "Create & develop a unique design from conceptial to final design drawings.";
  private categoryImg: string = "buildingBg";
  private currentLang = this.getCurrentLang();

  constructor() {
  }

  ngOnInit() {
    this.myInnerHeight = (window.innerHeight);
  }

  changeTab(categoryInfo) {
    this.categoryDescHeader = categoryInfo['descHeader'];
    this.categoryDesc = categoryInfo['desc'];
    this.categoryImg = categoryInfo['img'];
  }


  getCurrentLang() {
    return localStorage.getItem('currentLang');
  }
}



