import { Component, OnInit } from '@angular/core';
import { serviceUrls } from '../service-urls';
import { Users } from '../services/service.get-users';
import { User } from '../models/model.user';

@Component({
  selector: 'app-my-profile',
  templateUrl: './my-profile.component.html',
})
export class MyProfileComponent implements OnInit {

  editable: boolean = false;

  private currentUserId: number = 0;
  private currentUserEmail: string = "";

  private currentLoggedInUser = new User();
  constructor(private _Users: Users) { }

  ngOnInit() {
    this.getCurrentUserInfo();
  }

  edit() {
    this.editable = !this.editable;
  }

  save() {
    this.updateUser();
  }

  getCurrentUserInfo() {
    this._Users.getCurrentUser(serviceUrls.getCurrentUser).subscribe(
      data => {
        if (data["id"]) {
          this.currentUserId = data["id"];
          this.currentUserEmail = data['email'];
          this.currentLoggedInUser = data;
        }
      }
    );
  }

  updateUser() {
    this._Users.updateUser(serviceUrls.updateUser, this.currentLoggedInUser, this.currentUserId).subscribe(
      data => {
        if (this.currentUserEmail != this.currentLoggedInUser.email) {
          window.location.href = "/logout";
        } else {
          window.location.href = "/myProfile";
        }
      },
      error => {
        console.log(error + "----------------");
      }
    );
  }

}



