import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import { RequiredDrawings, BuildingTypes } from '../../drop-down-lists/workshop-drawings';
import { Visualization } from '../models/model.visualization';
import { FormSubmit } from '../services/service.submit-form';
import { serviceUrls } from '../../service-urls';
import { Users } from '../../services/service.get-users';
import { FileUpload } from '../services/service.file-upload';

@Component({
  selector: 'app-3d-visualization',
  templateUrl: './3d-visualization.component.html',
  providers: [FormSubmit, FileUpload]
})
export class VisualizationComponent implements OnInit {

  private formData;

  private visualizationModel = new Visualization();

  private subCatSelected = "";

  private externalView: boolean = false;
  private plansView: boolean = false;
  private interorView: boolean = false;

  constructor(private _FormSubmit: FormSubmit, private _Users: Users, private _FileUpload: FileUpload) { }

  private otherText = false;
  private validForm = true;
  private viewOnly: boolean = false;
  private disableRequiredDrawing = false;
  private orderStatus: string = "pending";

  private currentUser;
  private attachments: string[] = [];
  private uploadedFiles;

  private drawingsList = RequiredDrawings;
  private buildingTypes = BuildingTypes;
  private buildingTypesSub = [];

  selectSubCat() {
    if (this.subCatSelected == "external-view") {
      this.externalView = true;
      this.interorView = false;
      this.plansView = false;
    } else if (this.subCatSelected == "interior-design") {
      this.externalView = false;
      this.interorView = true;
      this.plansView = false;
    } else {
      this.externalView = false;
      this.interorView = false;
      this.plansView = true;
    }
  }

  ngOnInit() {
    if (localStorage.getItem('formData')) {
      this.disableRequiredDrawing = true;
      this.orderStatus = localStorage.getItem('orderStatus');
      this.attachments = localStorage.getItem('attachments').split(",");
      this.visualizationModel = JSON.parse(localStorage.getItem('formData'));
      this.checkForm();
      if (localStorage.getItem('orderStatus') != 'processed') {
        this.viewOnly = false;
      } else {
        this.viewOnly = true;
      }
      if (this.visualizationModel.buildingType == "Other") {
        this.otherText = true;
      } else {
        this.buildingTypesSub.push(this.visualizationModel.buildingTypeSub);
      }
    }
    this.getCurrentUserInfo();
  }

  checkForm() {
    this.subCatSelected = this.visualizationModel['drawings'];
    if (this.subCatSelected == "external-view") {
      this.externalView = true;
      this.interorView = false;
      this.plansView = false;
    } else if (this.subCatSelected == "interior-design") {
      this.externalView = false;
      this.interorView = true;
      this.plansView = false;
    } else {
      this.externalView = false;
      this.interorView = false;
      this.plansView = true;
    }
  }

  selectBuildingType(buildingSelected: string) {
    if (buildingSelected == "Other") {
      this.otherText = true;
      this.visualizationModel.buildingTypeSub = "";
      this.buildingTypesSub = [];
    } else {
      this.otherText = false;
      this.buildingTypesSub = buildingSelected.split(",");
    }
  }

  getFiles(e) {
    this.uploadedFiles = e;
    this.attachments = [];
    for (var file of e.target.files) {
      this.attachments.push(file.name);
    }
    console.log(this.attachments.toString());
  }

  submitForm() {
    this.validateData();
  }

  validateData() {
    if (this.visualizationModel.buildingTypeSub
      && this.visualizationModel.city
      && this.visualizationModel.country
      && this.visualizationModel.avgArea
      && this.visualizationModel.language && this.attachments) {
      this.validForm = true;
      this.saveForm(JSON.stringify(this.visualizationModel));
    } else {
      this.validForm = false;
    }
  }

  saveForm(formData) {
    this._FileUpload.uploadFiles(this.uploadedFiles).subscribe(
      data => {
        // SHOW A MESSAGE RECEIVED FROM THE WEB API.
        if (data) {
          this._FormSubmit.saveFormDetails(serviceUrls.addOrder, this.currentUser['id'], this.orderStatus, "3d-visualization", this.attachments.toString(), formData).subscribe(
            data => {
              window.location.href = "/myOrders";
            }, error => {
              console.error(error);
            }
          )
        }
      },
      error => {
        console.log(error.MESSAGE);    // SHOW ERRORS IF ANY.
      }
    );
  }
  ngOnDestroy() {
    localStorage.setItem('formData', "");
    localStorage.setItem('orderStatus', "");
    localStorage.setItem('attachments', "");
  }

  getCurrentUserInfo() {
    this._Users.getCurrentUser(serviceUrls.getCurrentUser).subscribe(
      data => {
        if (data["id"]) {
          this.currentUser = data;
        }
      }
    );
  }

}



