import { Component, OnInit } from '@angular/core';
import { serviceUrls } from '../service-urls';
import { Users } from '../services/service.get-users';
import { Logout } from '../services/service.logout';
import { TranslateService } from '../translate/translate.service';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  providers: [Logout]
})
export class HeaderComponent implements OnInit {
  private openSearch: boolean;
  private selectedItem: any;

  private currentUserName: string = "";
  private currentLang: string = "";
  private languageText: string = "";

  private loggedIn: boolean = false;
  constructor(private _Users: Users, private _Logout: Logout, private _translate: TranslateService) { this.openSearch = false }

  ngOnInit() {
    this.currentLang = this.getCurrentLang();
    this.getCurrentUserInfo();
    this.setLanguageText();
  }

  setLanguageText() {
    if (this.currentLang == "ar") {
      this.languageText = "English";
      // this.setArCss();
    } else {
      this.languageText = "";
      // this.setEnCss();
    }
    // this._translate.use(this.currentLang);
    // this.setCurrentLang(this.currentLang);
  }

  toggleLanguage() {
    if (this.currentLang == "en") {
      this.currentLang = "ar";
    } else {
      this.currentLang = "en";
    }
    this.setCurrentLang(this.currentLang);
  }

  toggleField() {
    this.openSearch = !this.openSearch;
  }

  setEnCss() {
    window['changeCSS']('/assets/bootstrap/css/bootstrap.min.css', 2);
    window['changeCSS']('/assets/styles.css', 3);
  }

  setArCss() {
    window['changeCSS']('/assets/bootstrap/css/bootstrap-rtl.min.css', 2);
    window['changeCSS']('/assets/styles-rtl.css', 3);
  }

  getCurrentUserInfo() {
    this._Users.getCurrentUser(serviceUrls.getCurrentUser).subscribe(
      data => {
        console.log(data);
        if (data["id"]) {
          this.loggedIn = true;
          this.currentUserName = data['firstName'];
        } else {
          this.loggedIn = false;
        }
      }
    );
  }

  logOut() {
    this._Logout.logOut(serviceUrls.logout).subscribe(
      data => {
        localStorage.clear();
        window.location.href = "/login";
      }, error => {
        console.log(error);
      }
    );
  }

  getCurrentLang() {
    return localStorage.getItem('currentLang');
  }

  setCurrentLang(language) {
    localStorage.setItem('currentLang', language);
    window.location.reload();
  }

  // activateClass(subModule){
  //   subModule.active = !subModule.active;    
  // }

}



