export class Visualization {
    constructor() {
        this.drawings = "";
        this.noOfLayouts = "";
        this.avgArea = "";
        this.buildingType = "";
        this.buildingTypeSub = "";
        this.country = "";
        this.city = "";
        this.planAttachment = "";
        this.language = "";
    }

    drawings: string;
    noOfLayouts: string;
    avgArea: string;
    buildingType: string;
    buildingTypeSub: string;
    country: string;
    city: string;
    planAttachment: string;
    language: string;

}


