export class FacadeType {
    constructor() {
        this.drawings = "";
        this.facadeType = "";
        this.buildingType = "";
        this.buildingTypeSub = "";
        this.buildingHeight = "";
        this.buildingArea = "";
        this.country = "";
        this.noOfFloors = "";
        this.architecturalStyle = "";
        this.city = "";
        this.additionalRequests = "";
        this.language = "";
    }

    drawings: string;
    facadeType: string;
    buildingType: string;
    buildingTypeSub: string;
    buildingHeight: string;
    buildingArea: string;
    country: string;
    noOfFloors: string;
    architecturalStyle: string;
    city: string;
    additionalRequests: string;
    language: string;

}


