export class ExternalView {
    constructor() {
        this.drawings = "";
        this.buildingType = "";
        this.buildingTypeSub = "";
        this.country = "";
        this.city = "";
        this.planAttachment = "";
        this.language = "";
        this.spaceArea = "";
        this.noOfFloors = "";
    }

    drawings: string;
    buildingType: string;
    buildingTypeSub: string;
    country: string;
    city: string;
    planAttachment: string;
    language: string;
    spaceArea: string;
    noOfFloors: string;

}


