import { ModuleWithProviders } from  '@angular/core';
import { Routes, RouterModule} from '@angular/router';
import { HomeContentComponent } from './home-content/home-content.component';
import { ServiceDetailsComponent } from './service-details/service-details.component';
import { RegisterationComponent } from './registeration/registeration.component';
import { AboutUsComponent } from './about-us/about-us.component';
import { ContactUsComponent } from './contact-us/contact-us.component';
import { WhatWeDoComponent } from './what-we-do/what-we-do.component';
import { HowWeWorkComponent } from './how-we-work/how-we-work.component';
import { OurProjectsComponent } from './our-projects/our-projects.component';
import { WorkshopDrawingComponent } from './forms/workshop-drawing/workshop-drawing.component';
import { StructuralDesignComponent } from './forms/structural-design/structural-design.component';
import { VisualizationComponent } from './forms/3d-visualization/3d-visualization.component';
import { MyProfileComponent } from './my-profile/my-profile.component';
import { MyOrdersComponent } from './my-orders/my-orders.component';
import { MechanicalDesignComponent } from './forms/mechanical-design/mechanical-design.component';
import { ContestCostComponent } from './forms/contest-cost/contest-cost.component';
import { EvacuationReportComponent } from './forms/safety/evacuation-report/evacuation-report.component';
import { FireSystemsComponent } from './forms/safety/fire-systems/fire-systems.component';
import { SafetArchitectComponent } from './forms/safety/safet-architect/safet-architect.component';
import { SafetyReportComponent } from './forms/safety/safety-report/safety-report.component';
import { CompleteDesignComponent } from './forms/building-design/complete-design/complete-design.component';
import { DesignConceptComponent } from './forms/building-design/design-concept/design-concept.component';
import { FacadeDesignComponent } from './forms/building-design/facade-design/facade-design.component';
import { WorkshopSlabsComponent } from './forms/workshop-drawing/workshop-slabs/workshop-slabs.component';
import { TendersRevisionComponent } from './forms/tenders-revision/tenders-revision.component';
import { TendersSeperateComponent } from './forms/tenders-seperate/tenders-seperate.component';
import { ExternalViewComponent } from './forms/3d-visualization/external-view/external-view.component';
import { TendersConsultComponent } from './forms/tenders-consult/tenders-consult.component';
import { WorkshopConsultComponent } from './forms/workshop-drawing/workshop-consult/workshop-consult.component';
import { DesignConsultComponent } from './forms/building-design/design-consult/design-consult.component';
import { SafetyConsultComponent } from './forms/safety/safety-consult/safety-consult.component';



const appRoutes: Routes = [
    {
        path: '',
        component: HomeContentComponent,
    },
    {
        path: 'home',
        component: HomeContentComponent,
    },
    {
        path: 'service/:category',
        component: ServiceDetailsComponent,
    },
    {
        path: 'register',
        component: RegisterationComponent,
    },
    {
        path: 'forms/SHOP-DRAWINGS',
        component: WorkshopDrawingComponent,
    },
    {
        path: 'forms/Workshop-drawings-for-pre-stressed',
        component: WorkshopSlabsComponent,
    },
    {
        path: 'forms/structural-design',
        component: StructuralDesignComponent,
    },
    {
        path: 'forms/3d-visualization',
        component: VisualizationComponent,
    },
    {
        path: 'forms/mechanical-design',
        component: MechanicalDesignComponent,
    },
    {
        path: 'forms/Bidding-price',
        component: ContestCostComponent,
    },
    {
        path: 'forms/Review-and-check',
        component: TendersRevisionComponent,
    },
    {
        path: 'forms/External-view',
        component: ExternalViewComponent,
    },
    {
        path: 'forms/Tenders-seperate',
        component: TendersSeperateComponent,
    },
    {
        path: 'aboutUs',
        component: AboutUsComponent,
    },
    {
        path: 'contactUs',
        component: ContactUsComponent,
    },
    {
        path: 'what-we-do',
        component: WhatWeDoComponent,
    },
    {
        path: 'myProfile',
        component: MyProfileComponent,
    },
    {
        path: 'myOrders',
        component: MyOrdersComponent,
    },
    {
        path: 'how-we-work',
        component: HowWeWorkComponent,
    },
    {
        path: "our-projects",
        component: OurProjectsComponent
    },
    {
        path: "forms/safety-report",
        component: SafetyReportComponent
    },
    {
        path: "forms/safety-architectrual",
        component: SafetArchitectComponent
    },
    {
        path: "forms/fire-systems",
        component: FireSystemsComponent
    },
    {
        path: "forms/evacuation",
        component: EvacuationReportComponent
    },
    {
        path: "forms/complete-design",
        component: CompleteDesignComponent
    },
    {
        path: "forms/design-concept",
        component: DesignConceptComponent
    },
    {
        path: "forms/facade-design",
        component: FacadeDesignComponent
    },
    {
        path: "forms/tenders-consult",
        component: TendersConsultComponent
    },
    {
        path: "forms/workshop-consult",
        component: WorkshopConsultComponent
    },
    {
        path: "forms/design-consult",
        component: DesignConsultComponent
    },
    {
        path: "forms/safety-consult",
        component: SafetyConsultComponent
    }
];
export const routing: ModuleWithProviders = RouterModule.forRoot(appRoutes);


