export class ContestCost {
    constructor() {

        this.services  = '';
        this.city = '';
        this.projectSize = '';
        this.buildingSize = '';
        this.buildingType = '';
        this.buildingTypeSub = '';
        this.noOfFloors = '';
        this.additionalRequests = '';
        this.country = '';

    }

    services: string;
    city: string;
    projectSize: string;
    buildingSize: string;
    buildingType: string;
    buildingTypeSub: string;
    noOfFloors: string;
    additionalRequests: string;
    country: string;
}



