import { Component, OnInit } from '@angular/core';
import { User } from '../models/model.user';
import { Users } from '../services/service.get-users';
import { serviceUrls } from '../service-urls';

@Component({
  selector: 'app-registeration',
  templateUrl: './registeration.component.html',
  providers: [Users]
})
export class RegisterationComponent implements OnInit {

  private user: User = new User();

  //Form Validators
  private firstNameInValid = false;
  private lastNameInValid = false;
  private emailInValid = false;
  private passwordInValid = false;
  private confirmPasswordInValid = false;
  private mobileInvalid = false;
  private passwordMatch = false;

  private confirmPassword: string = "";

  private errors: number = 0;

  constructor(private _Users: Users) { }

  ngOnInit() {
  }

  validateInformation() {
    this.errors = 0;
    if (!this.user.firstName) {
      this.firstNameInValid = true;
      this.errors++;
    } else {
      this.firstNameInValid = false;
    }
    if (!this.user.mobile) {
      this.mobileInvalid = true;
      this.errors++;
    } else {
      this.mobileInvalid = false;
    }
    if (!this.user.lastName) {
      this.lastNameInValid = true;
      this.errors++;
    } else {
      this.lastNameInValid = false;
    }
    if (!this.user.email) {
      this.emailInValid = true;
      this.errors++;
    } else {
      this.emailInValid = false;
    }
    if (!this.user.password) {
      this.passwordInValid = true;
      this.errors++;
    } else {
      this.passwordInValid = false;
    }
    if (!this.confirmPassword) {
      this.confirmPasswordInValid = true;
      this.errors++;
    } else if (this.confirmPassword && this.confirmPassword != this.user.password) {
      this.confirmPasswordInValid = false;
      this.passwordMatch = true;
      this.errors++;
    } else if (this.confirmPassword && this.confirmPassword == this.user.password) {
      this.confirmPasswordInValid = false;
      this.passwordMatch = false;
    }
    this.registerUser();
  }

  registerUser() {
    if (this.errors == 0) {
      this.user.role = "ROLE_GUEST";
      this.addUser();
    }
  }

  addUser() {
    this._Users.addUser(serviceUrls.addUser, this.user).subscribe(
      data => {
        window.location.href = "/";
      },
      error => {
        console.log(error + "----------------");
      }
    );
  }
}



