export class User {
    constructor() {
        this.firstName = "";
        this.lastName = "";
        this.email = "";
        this.mobile = "";
        this.password = "";
        this.role = "";
    }

    firstName: string;
    lastName: string;
    email: string;
    mobile: string;
    password: string;
    role: string;

}


