export class WorkShopDrawings {
    constructor() {
        this.drawings = "";
        this.buildingType = "";
        this.buildingTypeSub = "";
        this.country = "";
        this.noOfFloors = "";
        this.city = "";
        this.additionalRequests = "";
        this.language = "";
    }

    drawings: string;
    buildingType: string;
    buildingTypeSub: string;
    country: string;
    noOfFloors: string;
    city: string;
    additionalRequests: string;
    language: string;

}


