export class CompleteDesign {
    constructor() {
        this.drawings = "";
        this.buildingType = "";
        this.buildingTypeSub = "";
        this.landArea = "";
        this.buildingArea = "";
        this.country = "";
        this.noOfFloors = "";
        this.city = "";
        this.additionalRequests = "";
        this.language = "";
    }

    drawings: string;
    buildingType: string;
    buildingTypeSub: string;
    landArea: string;
    buildingArea: string;
    country: string;
    noOfFloors: string;
    city: string;
    additionalRequests: string;
    language: string;

}


