import { Injectable } from "@angular/core"
import { Http, Headers } from '@angular/http';

import 'rxjs/add/operator/map';

@Injectable()
export class FormSubmit {

    constructor(private http: Http) { }

    saveFormDetails(url: string, userId: string, status: string,  name: string, attachment: string, formData) {

        var parameters = {
            name: name,
            creationDate:"",
            status: status,
            attachment: attachment,
            formData: formData,
            userId: userId
        };

        return this.http.post(url, parameters).map(res => res.text());
    }
}


